import {useState} from 'react';
import LoginComponent from './login.component';
import RegisterComponent from './register.component';
import { openSnackBar } from '../../redux/snackBarReducer';
import { useDispatch } from 'react-redux';

const Auth = () => {
  const [signFlag, setSignFlag] = useState(false); // false: signin, true: signup
  const [role, setRole] = useState('merchant');  
  const [currentGroup, setCurrentGroup] = useState(0);
  const dispatch = useDispatch();

  const switchLogin = () => {
    setSignFlag(false);
  }

  const switchRegister = () => {
    setSignFlag(true);
  }

  const handleRole = (roleValue) => {
    if (currentGroup > 0) {
      // dispatch(openSnackBar({ message: 'Cannot exchange role.', status: 'warning' })); 
      return;
    }
    setRole(roleValue);
  }

  return (
    <div className='w-full h-[100vh] flex justify-center items-center'>
        <div className='w-[50vw] max-w-[1000px] min-w-[380px] min-h-[460px] rounded-[1rem] border border-white'>
            <div className='flex border-b border-gray-300'>
                <button 
                    className={`w-full text-xl leading-[3.6rem] rounded-t-[1rem] ${role === 'merchant' ? 'bg-[#D9D9D9] text-gray-800' : 'bg-transparent text-gray-100'}`}
                    onClick={() => handleRole('merchant')}
                >Merchants</button>
                <button 
                    className={`w-full text-xl leading-[3.6rem] rounded-t-[1rem] ${role === 'customer' ? 'bg-[#D9D9D9] text-gray-800' : 'bg-transparent text-gray-100'}`}
                    onClick={() => handleRole('customer')}
                >Customers</button>
            </div>
            {signFlag ? <RegisterComponent switchLogin={switchLogin} role={role} currentGroup={currentGroup} setCurrentGroup={setCurrentGroup}/> : <LoginComponent switchRegister={switchRegister} role={role} currentGroup={currentGroup} setCurrentGroup={setCurrentGroup}/>}
        </div>
    </div>
  );
}

export default Auth;