import { Fragment, createRef, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Dialog, Transition } from '@headlessui/react';
import { userService } from '../../services/users.service';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation } from 'react-router-dom';
import { openSnackBar } from '../../redux/snackBarReducer';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEnvelope, faLock, faCheckDouble, faEye, faEyeSlash } from '@fortawesome/free-solid-svg-icons';
import { logUserIn, selectRegistering, selectLoggedIn, forgotPassword } from '../../redux/authReducer';

const LoginComponent = ({switchRegister, role}) => {

  const navigate = useNavigate();
  const dispatch = useDispatch();
  
  const location = useLocation();
  const { from } = location.state || { from: { pathname: "/" } };
  
  const isLoggedIn = useSelector(selectLoggedIn);

  const [email, setEmail] = useState('');
  const [emailIssue, setEmailIssue] = useState(false);
  const [password, setPassword] = useState('');
  const [passwordIssue, setPasswordIssue] = useState(false);

  useEffect(() => {
    if (isLoggedIn) {
      navigate('/');
    }
  }, [isLoggedIn])

  useEffect(() => {
    setEmail('');
    setPassword('');
  }, [])

  const checkGroupValidation = (index) => {
    const ret = {message: "", status: false};

    switch (index) {
      case 0:
        if (email.length === 0) {ret.message = "Please input email"; ret.status = false; setEmailIssue(true)}
        else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) {ret.message = "Invalid Email!"; ret.status = false; setEmailIssue(true)}
        else if (password.length === 0) {ret.message = "Please input password"; ret.status = false; setPasswordIssue(true)}
        else { ret.message = ""; ret.status = true;}
        break;
    }

    return ret;
  }

  const loginProc = async () => {
    
    const checking = checkGroupValidation(0);
    if (!checking.status) {
      dispatch(openSnackBar({ message: checking.message, status: 'error' }));  
      return;
    }

    dispatch(logUserIn(email, password, role, from.pathname));
  }

  const handleForgotPassword = async () => {
    const ret = {message: "", status: true};
    if (email.length === 0) {ret.message = "Please input email"; ret.status = false; setEmailIssue(true);}
    else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) {ret.message = "Invalid Email!"; ret.status = false; setEmailIssue(true)} 
    if (!ret.status) {
      dispatch(openSnackBar({ message: ret.message, status: 'error' }));  
      return;
    }
    dispatch(forgotPassword(email, role, from.pathname));
  }

  return (
    <div className="w-full px-[3rem] text-xl">
      <div className="flex flex-col my-[1rem] mb-[2rem] mt-[2rem]">
        <label className="block uppercase tracking-wide text-gray-100 mb-2">
          Email:
        </label>
        <input 
            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
            type="email"
            value={email}
            onChange={e => {setEmail(e.target.value); setEmailIssue(false)}} />
        <p className={`${emailIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>Email Address is not recognised. Please check again or register.</p>
      </div>
      <div className="flex flex-col my-[1rem] mb-[0.1rem]">
        <label className="block uppercase tracking-wide text-gray-100 mb-2">
          Password:
        </label>
        <input 
            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
            type="password"
            value={password}
            onChange={e => {setPassword(e.target.value); setPasswordIssue(false)}} />
        <p className={`${passwordIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>Password incorrect/not found. Please check and try again.</p>
      </div>
      <div className="flex flex-col mb-[2rem]">
        <label className="block uppercase tracking-wide text-gray-100 text-right underline italic cursor-pointer"
          onClick={() => handleForgotPassword()}>
          Forgot Password?
        </label>
      </div>
      <div className='flex justify-between'>
          <button 
              className={`w-[8rem] text-xl text-gray-100 text-left underline decoration-1 leading-[2.6rem]`}
              onClick={() => switchRegister()}
              
          >Register</button>
          <button 
              className={`w-[8rem] text-xl leading-[2.6rem] rounded-[0.6rem] bg-[#D9D9D9] text-gray-800`}
              onClick={() => loginProc()}
          >Login</button>
      </div>
    </div>
  );
};

export default LoginComponent;