import { Fragment, createRef, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Dialog, Transition } from '@headlessui/react';
import { userService } from '../../services/users.service';
import { useDispatch, useSelector } from 'react-redux';
import { openSnackBar } from '../../redux/snackBarReducer';
import PasswordStrengthBar from 'react-password-strength-bar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEnvelope, faLock, faCheckDouble, faEye, faEyeSlash } from '@fortawesome/free-solid-svg-icons';
import { registerUser, selectRegistering, selectRegisterSuccess } from '../../redux/authReducer';
import Select from 'react-select';
import { NATIONALITIES }  from '../../config/constants';

const RegisterComponent = ({switchLogin, role, currentGroup, setCurrentGroup}) => {

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const registering = useSelector(selectRegistering);
  const successRegistered = useSelector(selectRegisterSuccess);
  const [regproc, setRegproc] = useState(false);

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [repassword, setRepassword] = useState('');
  const [businessname, setBusinessname] = useState('');
  const [address, setAddress] = useState('');
  const [postcode, setPostcode] = useState('');
  const [telephone, setTelephone] = useState('');
  const [notes, setNotes] = useState('');
  const [userproxy, setUserProxy] = useState([]);
  const [username, setUsername] = useState([]);
  const [userpin, setUserpin] = useState([]);
  const [emailIssue, setEmailIssue] = useState(false);
  const [passwordIssue, setPasswordIssue] = useState(false);
  const [repasswordIssue, setRepasswordIssue] = useState(false);
  const [businessIssue, setBusinessIssue] = useState(false);
  const [addressIssue, setAddressIssue] = useState(false);
  const [postcodeIssue, setPostcodeIssue] = useState(false);
  const [telephoneIssue, setTelephoneIssue] = useState(false);
  const [notesIssue, setNotesIssue] = useState(false);
  const [usernameIssue, setUsernameIssue] = useState(false);
  const [pinIssue, setPinIssue] = useState(false);
  
  const form_groups = role === 'merchant' ? [
    <div key={1} className='w-full h-full'>
      <div className="flex flex-col mb-[0.5rem] mt-[1rem]">
        <label className="block uppercase tracking-wide text-gray-100 mb-1">
          Email:
        </label>
        <input 
            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
            type="email"
            value={email}
            onChange={e => {setEmail(e.target.value); setEmailIssue(false)}} />
        <p className={`${emailIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>Email format incorrect. Please check and try again.</p>
      </div>
      <div className="flex flex-col mb-[0.5rem]">
        <label className="block uppercase tracking-wide text-gray-100 mb-1">
          Password:
        </label>
        <input 
            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
            type="password"
            value={password}
            onChange={e => {setPassword(e.target.value); setPasswordIssue(false)}} />
        <p className={`${passwordIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>{'Password must be alphanumeric, > 8 characters and contain one symbol.'}</p>
      </div>
      <div className="flex flex-col mb-[0.5rem]">
        <label className="block uppercase tracking-wide text-gray-100 mb-1">
          Confirm Password:
        </label>
        <input 
            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
            type="password"
            value={repassword}
            onChange={e => {setRepassword(e.target.value); setRepasswordIssue(false)}} />
        <p className={`${repasswordIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>Passwords do not match</p>
      </div>
    </div>,
    <div key={2} className='w-full h-full'>
      <div className="flex flex-col mb-[0.5rem] mt-[1rem]">
        <label className="block uppercase tracking-wide text-gray-100 mb-1">
          Business Name:
        </label>
        <input 
            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
            type="text"
            value={businessname}
            onChange={e => {setBusinessname(e.target.value); setBusinessIssue(false)}} />
        <p className={`${businessIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>{'Business Name must be >2 characters'}</p>
      </div>
      <div className="flex flex-col mb-[0.5rem]">
        <label className="block uppercase tracking-wide text-gray-100 mb-1">
          Address:
        </label>
        <input 
            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
            type="text"
            value={address}
            onChange={e => {setAddress(e.target.value); setAddressIssue(false)}} />
        <p className={`${addressIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>Address must not be empty</p>
      </div>
      <div className="flex flex-col mb-[0.5rem]">
        <label className="block uppercase tracking-wide text-gray-100 mb-1">
          Postcode:
        </label>
        <input 
            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
            type="text"
            value={postcode}
            onChange={e => {setPostcode(e.target.value); setPostcodeIssue(false)}} />
        <p className={`${postcodeIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>Postcode must not be empty</p>
      </div>
    </div>,
    // <div key={3} className='w-full h-full'>
    //   <div className='text-2xl font-bold mt-4 text-gray-100'>Add Users?</div>
    //   <div className='w-full h-[13rem] overflow-y-auto'>
    //     {
    //       userproxy.map((up, index) => {
    //         return (
    //           <div key={index}>
    //             <div className="flex flex-row items-center">
    //               <div className="flex flex-col mb-[0.5rem] mt-[1rem] w-[55%]">
    //                 <div className="flex flex-row items-center">
    //                   <label className="block uppercase tracking-wide text-gray-100 mb-1">
    //                     Name:
    //                   </label>
    //                   <div 
    //                       className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
    //                   >{up.username}</div>
    //                 </div>
    //               </div>
    //               <div className="flex flex-col mb-[0.5rem] mt-[1rem] w-[40%]">
    //                 <div className="flex flex-row items-center">
    //                   <label className="block uppercase tracking-wide text-gray-100 mb-1">
    //                     PIN:
    //                   </label>
    //                   <div 
    //                       className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
    //                   >{up.userpin}</div>
    //                 </div>
    //               </div>
    //             </div>
    //           </div>
    //         )
    //       })
    //     }
    //     <div className="flex flex-row items-center">
    //       <div className="flex flex-col mb-[0.5rem] mt-[1rem] w-[55%]">
    //         <div className="flex flex-row items-center">
    //           <label className="block uppercase tracking-wide text-gray-100 mb-1">
    //             Name:
    //           </label>
    //           <input 
    //               className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
    //               type="text"
    //               value={username}
    //               onChange={e => {setUsername(e.target.value); setUsernameIssue(false)}} />
    //         </div>
    //         <p className={`${usernameIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>{'Name must be >2 characters'}</p>
    //       </div>
    //       <div className="flex flex-col mb-[0.5rem] mt-[1rem] w-[40%]">
    //         <div className="flex flex-row items-center">
    //           <label className="block uppercase tracking-wide text-gray-100 mb-1">
    //             PIN:
    //           </label>
    //           <input 
    //               className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
    //               type="text"
    //               value={userpin}
    //               onChange={e => {setUserpin(e.target.value); setPinIssue(false)}} />
    //         </div>
    //         <p className={`${pinIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>PIN should be 4 digits</p>
    //       </div>
    //       <div className="flex flex-col mb-[0.5rem] mt-[1rem] w-[5%]">
    //         <button 
    //             className={`w-[2rem] h-[2rem] text-xl rounded-[0.6rem] leading-[2rem] bg-[#D9D9D9] text-gray-800`}    
    //             onClick={() => addUserProxy()}        
    //         >+</button>
    //       </div>
    //     </div>
    //   </div>
    //   <div className='text-xl text-gray-100 mt-[3rem]'>Click next if no other users needed</div>
    // </div>
  ] : [
    <div key={1} className='w-full h-full'>
      <div className="flex flex-col mb-[0.5rem] mt-[1rem]">
        <label className="block uppercase tracking-wide text-gray-100 mb-1">
          Email:
        </label>
        <input 
            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
            type="email"
            value={email}
            onChange={e => {setEmail(e.target.value); setEmailIssue(false)}} />
        <p className={`${emailIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>Email format incorrect. Please check and try again.</p>
      </div>
      <div className="flex flex-col mb-[0.5rem]">
        <label className="block uppercase tracking-wide text-gray-100 mb-1">
          Password:
        </label>
        <input 
            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
            type="password"
            value={password}
            onChange={e => {setPassword(e.target.value); setPasswordIssue(false)}} />
        <p className={`${passwordIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>{'Password must be alphanumeric, > 8 characters and contain one symbol.'}</p>
      </div>
      <div className="flex flex-col mb-[0.5rem]">
        <label className="block uppercase tracking-wide text-gray-100 mb-1">
          Confirm Password:
        </label>
        <input 
            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
            type="password"
            value={repassword}
            onChange={e => {setRepassword(e.target.value); setRepasswordIssue(false)}} />
        <p className={`${repasswordIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>Passwords do not match</p>
      </div>
    </div>,
    <div key={2} className='w-full h-full'>
      <div className="flex flex-col mb-[0.5rem] mt-[1rem]">
        <label className="block uppercase tracking-wide text-gray-100 mb-1">
          Username:
        </label>
        <input 
            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
            type="text"
            value={businessname}
            onChange={e => {setBusinessname(e.target.value); setBusinessIssue(false)}} />
        <p className={`${businessIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>{'Username must be >2 characters'}</p>
      </div>
      <div className="flex flex-col mb-[0.5rem]">
        <label className="block uppercase tracking-wide text-gray-100 mb-1">
          Address:
        </label>
        <input 
            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
            type="text"
            value={address}
            onChange={e => {setAddress(e.target.value); setAddressIssue(false)}} />
        <p className={`${addressIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>Address must not be empty</p>
      </div>
      <div className="flex flex-col mb-[0.5rem]">
        <label className="block uppercase tracking-wide text-gray-100 mb-1">
          Postcode:
        </label>
        <input 
            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
            type="text"
            value={postcode}
            onChange={e => {setPostcode(e.target.value); setPostcodeIssue(false)}} />
        <p className={`${postcodeIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>Postcode must not be empty</p>
      </div>
    </div>,
    <div key={3} className='w-full h-full'>
      <div className="flex flex-col mb-[0.5rem] mt-[1rem]">
        <label className="block uppercase tracking-wide text-gray-100 mb-1">
          Telephone:
        </label>
        <input 
            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
            type="text"
            value={telephone}
            onChange={e => {setTelephone(e.target.value); setTelephoneIssue(false)}} />
        <p className={`${telephoneIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>Telephone must not be empty</p>
      </div>
      <div className="flex flex-col mb-[6.6rem]">
        <label className="block uppercase tracking-wide text-gray-100 mb-1">
          Notes:
        </label>
        <input 
            className="appearance-none bg-transparent border-b border-gray-300 w-full text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
            type="text"
            value={notes}
            onChange={e => {setNotes(e.target.value); setNotesIssue(false)}} />
        <p className={`${notesIssue ? 'text-red-400':'text-[#404040]'} text-sm select-none`}>Notes must not be empty</p>
      </div>
    </div>
  ];

  const refs = form_groups.reduce((acc, val, i) => {
    acc[i] = createRef();
    return acc;
  }, {});

  const totalGroups = form_groups.length;
  const scrollToGroup = i => {
    setCurrentGroup(i);
    refs[i].current.scrollIntoView({
      behavior: 'smooth',
      block: 'nearest',
      inline: 'start',
    });
  };
  const nextGroup = () => {
    if (currentGroup >= totalGroups - 1) {
      //scrollToGroup(0);
      return;
    } else {
      scrollToGroup(currentGroup + 1);
    }
  };
  const previousGroup = () => {
    if (currentGroup === 0) {
      //scrollToGroup(totalGroups - 1);
      return;
    } else {
      scrollToGroup(currentGroup - 1);
    }
  };

  const checkGroupValidation = (index) => {
    const ret = {message: "", status: false};

    switch (index) {
      case 0:
        if (email.length === 0) {ret.message = "Please input email"; ret.status = false; setEmailIssue(true)}
        else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) {ret.message = "Invalid Email!"; ret.status = false; setEmailIssue(true)}
        else if (password.length === 0) {ret.message = "Please input password"; ret.status = false; setPasswordIssue(true);}
        else if (password.length < 6) {ret.message = "Password must be > 5 characters"; ret.status = false; setPasswordIssue(true);}
        else if (repassword.length === 0) {ret.message = "Please type confirm password"; ret.status = false; setRepasswordIssue(true);}
        else if (password !== repassword) {ret.message = "Not match password"; ret.status = false; setRepasswordIssue(true);}
        else { ret.message = ""; ret.status = true;}
        break;
      case 1:
        if (businessname.length === 0) {ret.message = role === 'merchant' ? "Please input businessname":"Please input username"; ret.status = false; setBusinessIssue(true);}
        else if (address.length === 0) {ret.message = "Please input address"; ret.status = false; setAddressIssue(true);}
        else if (postcode.length === 0) {ret.message = "Please input postcode"; ret.status = false; setPostcodeIssue(true);}
        else { ret.message = ""; ret.status = true;}
        break;
      case 2:
        if (telephone.length === 0) {ret.message = "Please input telephone"; ret.status = false; setTelephoneIssue(true);}
        else if (notes.length === 0) {ret.message = "Please input notes"; ret.status = false; setNotesIssue(true);}
        else { ret.message = ""; ret.status = true;}
        break;
    }

    return ret;
  }

  const checkProxyValidation = () => {
    const ret = {message: "", status: false};

    if (username.length === 0) { setUsernameIssue(true); ret.status = false; }
    else if (username.length < 2) { setUsernameIssue(true); ret.status = false; }
    else if (userpin.length === 0) { setPinIssue(true); ret.status = false; }
    else if (isNaN(userpin) === true) { setPinIssue(true); ret.status = false; }
    else if (userpin.length < 4) { setPinIssue(true); ret.status = false; }
    else if (userpin.length > 4) { setPinIssue(true); ret.status = false; }
    else { ret.message = ""; ret.status = true;}

    return ret;
  }

  const nextGroupProc = async () => {
    if (currentGroup === 0) {
      const checking = checkGroupValidation(0);
      if (!checking.status) {
        dispatch(openSnackBar({ message: checking.message, status: 'error' }));  
        return;
      }

      // dispatch(openSnackBar({ message: `Checking user email.`, status: 'success' }));
      const res = await userService.checkUserByEmail(email, role);

      if (res.result) {
        dispatch(openSnackBar({ message: `User with email: ${email} already exists. Please check your email.`, status: 'error' }));
        return;        
      }
      // dispatch(openSnackBar({ message: `User with email: ${email} is available.`, status: 'success' }));
    }
    else if (currentGroup === 1) {
      const checking = checkGroupValidation(1);
      if (!checking.status) {
        dispatch(openSnackBar({ message: checking.message, status: 'error' }));  
        return;
      }
      if (role === 'merchant') {
        if (username.length > 0 || userpin.length > 0) {
          const checking = checkProxyValidation();
          if (!checking.status) {
              return;
          }
          const chk = checkDupUsername(username)
          if (chk) {
              dispatch(openSnackBar({ message: `User with username: ${username} already exists. Please retype your username.`, status: 'error' }));
              return;        
          }
        }
        dispatch(registerUser({
          businessname: businessname, 
          address: address, 
          postcode: postcode, 
          email: email, 
          password: password, 
          role: role, 
          userproxy: userproxy,
          username: username,
          userpin: userpin,
          token: undefined
        }));
      }
    }
    else if (currentGroup === 2) {
      if (role === 'customer') {
        const checking = checkGroupValidation(2);
        if (!checking.status) {
          dispatch(openSnackBar({ message: checking.message, status: 'error' }));  
          return;
        }
        dispatch(registerUser({
          businessname: businessname, 
          address: address, 
          postcode: postcode, 
          email: email, 
          password: password, 
          role: role, 
          userproxy: [],
          tel: telephone,
          notes: notes,
          username:'',
          userpin: '',
          token: undefined
        }));
      }

      setRegproc(true);
      return;
    }
    nextGroup();
  }

  const prevGroupProc = async () => {
    previousGroup();
  }

  const checkDupUsername = (username) => {    
    const tp = userproxy.filter((p) => p.username === username)
    if (tp.length > 0)
      return true;
    return false;
  }

  const addUserProxy = async () => {
    const checking = checkProxyValidation();
      if (!checking.status) {
        return;
    }

    const res = await userService.checkUserByProxyName(username);
    if (res.result) {
      dispatch(openSnackBar({ message: `User with username: ${username} already exists. Please retype your username.`, status: 'error' }));
      return;        
    }

    const chk = checkDupUsername(username)
    if (chk) {
      dispatch(openSnackBar({ message: `User with username: ${username} already exists. Please retype your username.`, status: 'error' }));
      return;        
    }

    let proxy = userproxy;
    proxy.push({username: username, userpin: userpin});
    setUserProxy(proxy);
    setUsername('')
    setUserpin('')
  }

  useEffect (() => {
    if (successRegistered) {
      navigate('/');
    }
  }, [successRegistered])

  return (
    <div className="w-full px-[3rem] text-xl">
      <div className='relative w-full h-full'>
        <div className="carousel w-full h-full">
          {form_groups.map((form_group, i) => {
            return (
              <div className='w-full h-full flex-shrink-0' key={i} ref={refs[i]}>  
                {form_group} 
              </div>
            )
          })}
        </div>
      </div>
      <div className='flex justify-between'>
          <button 
              className={`w-[5rem] text-xl text-gray-100 text-left underline decoration-1 leading-[2.6rem]`}
              onClick={() => currentGroup === 0 ? switchLogin() : prevGroupProc()}              
          >{currentGroup === 0 ? "Login" : "Back"}</button>
          <button 
              className={`w-[8rem] text-xl leading-[2.6rem] rounded-[0.6rem] bg-[#D9D9D9] text-gray-800`}
              onClick={() => nextGroupProc()}
          >Next</button>
      </div>
    </div>
  );
};

export default RegisterComponent;